
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}
.background-img {
  position: absolute;
  width: 150%;
  height: auto;
  margin-top: -10%;
  margin-left: -10%;
  background-size: cover;
  z-index: -1;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-filter: blur(20px);
          filter: blur(20px);
}
.disc {
  position: relative;
  margin-top: 20%;
  margin-left: 40%;
  width: 25rem;
  height: 25rem;
  border-radius: 25rem;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #000;
  background-image: radial-gradient(5em 30em ellipse, #fff, #000);
  border: 2px solid #131313;
  -webkit-box-shadow: 0 0 0 10px #343935;
          box-shadow: 0 0 0 10px #343935;
  opacity: 0.7;
}
.song-img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transform: translate(-50%, -50%) rotate(0deg);
          transform: translate(-50%, -50%) rotate(0deg);
  width: 15rem;
  height: 15rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid #000;
  border-radius: 15rem;
  -webkit-animation: animations1 10s linear infinite forwards;
          animation: animations1 10s linear infinite forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  background-image: url(/static/img/head.eeeb5a2.jpeg);
}
.lrc {
  position: absolute;
  right: 10%;
  top: 10%;
  overflow: auto;
  width: 20%;
  height: 50%;
  color: #fff;
}
.lrc-active {
  color: #f00;
  font-size: 20px;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
@-webkit-keyframes animations1 {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
            transform: translate(-50%, -50%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
}
}
@keyframes animations1 {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
50% {
    -webkit-transform: translate(-50%, -50%) rotate(180deg);
            transform: translate(-50%, -50%) rotate(180deg);
}
100% {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
}
}